
.home {
    height: 100%;
    margin: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center
}
@media screen and (min-width: 650px) {
#main-logo {
        font-size: 150px;
}
}
@media screen and (max-width: 649px) {
#main-logo {
        font-size: 75px;
}
}
@media screen and (min-width: 650px) {
#second-logo {
        font-size: 100px;
}
}
@media screen and (max-width: 500px) {
.no-padding-xs {
        position: absolute;
        left: 0;
        top: 10%;
        width: 100%;
        text-align: center;
        color: white;
        font-family: 'Lato', sans-serif;
        font-size: 50px;
        letter-spacing: 10px;
}
}
@media screen and (min-width: 501px) {
.extra-padding-md {
        position: absolute;
        left: 0;
        top: 20%;
        width: 100%;
        text-align: center;
        color: white;
        font-family: 'Lato', sans-serif;
        font-size: 50px;
        letter-spacing: 10px;
        padding: 50px 80px;
}
}

/* BACKGROUND PHOTOS */
.bgimg-1,
.bgimg-2,
.bgimg-3 {
    position: relative;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 0;
}
.bgimg-1 *,
.bgimg-2 *,
.bgimg-3 * {
    position: relative;
    z-index: 2 !important;
}
.bgimg-1:after,
.bgimg-2:after,
.bgimg-3:after {
    position: absolute;
    display: block;
    content: " ";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(245, 242, 242, .37);
    z-index: 1;
}
.bgimg-1 {
    background-image: url('../assets/Optimized-confer-main-photo.jpg');
    min-height: 1000px;
    margin-top: -50px;
}
.bgimg-2 {
    background-image: url('../assets/Optimized-conference.jpeg');
    min-height: 400px;
}
.bgimg-3 {
    background-image: url('../assets/Optimized-people.jpeg');
    min-height: 600px;
}
@media screen and (max-width: 500px) {
.my-img-responsive {
        display: block;
        max-width: 100%;
        height: auto;
}
}
.caption {
    position: absolute;
    left: 0;
    top: 20%;
    width: 100%;
    text-align: center;
    color: white;
    font-family: 'Lato', sans-serif;
    padding: 18px;
    font-size: 50px;
    letter-spacing: 10px;
}
.space-text {
    color: #fff;
    background-color: #111;
    font-family: 'Lato', sans-serif;
    letter-spacing: 5px;
}
.off-section {
    color: black;
    background-color: white;
    font-family: 'Lato', sans-serif;
    letter-spacing: 5px;
}
.extra-padding {
    padding: 50px 80px;
}
.main-heading {
    font-family: 'Abril Fatface', cursive;
    text-shadow: 2px 2px 10px black;
    color: white;
    margin-top: 250px;
}
.end-footer {
    margin-top: 200px;
    padding-bottom: 200px;
}
.welcome {
    font-family: 'Lato', sans-serif;
    text-shadow: 2px 2px 4px black;
    color: white;
    letter-spacing: 5px;
}
.navbar ul {
    list-style-type: none;
}
@media (max-width: 2000px) {
.navbar-header {
        float: none;
}
.navbar-toggle {
        display: block;
}
.navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}
.navbar-collapse.collapse {
        display: none!important;
}
.navbar-collapse.collapse.in {
        display: block!important;
}
.navbar-nav {
        float: none!important;
        margin: 7.5px -15px;
}
.navbar-nav>li {
        float: none;
}
.navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
}
.state {
        width: 30%;
}
.nav-drop-btn {
        width: 90%;
        background-color: #FFFFF9;
        margin: 2px;
        font: black;
}
